.Timer{
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.Range{
    width: 35px;
    height: 400px;
    background-color: rgb(78 185 238 / 32%);
    border-radius: 20px;
    box-shadow: 0 3px 6px #cacaca;
    display: flex;
    flex-direction: column-reverse;
    overflow-y: hidden;
}
.Flow{
    width: 35px;
    height: 0%;
    background-color: #4EB9EE;
    border-radius: 20px;
    box-shadow: 0 3px 6px #cacaca;
    transition: 0.3s;
}
.Complete{
    animation: complete 0.5s ease-out forwards;
}
@keyframes complete{
    0%{
        transform: scale(1);
    }
    50%{
        transform: scale(1.2);
    }
    100%{
        transform: scale(1.1);
    }
}