.Calculator{
    background: #323232;
    height: 100vh;
}
.Head{
    margin: 0;
    font-size: 2rem;
    color: #4D4D4D;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    text-shadow: 2px 2px 5px #00000073, -1px -1px 4px #6b6b6b3b;
    padding: 20px 0;
    animation: up 0.5s ease-out backwards;
}
@keyframes up{
    0%{
        text-shadow:none;
        transform: scale(0.98);
    }
    100%{
        text-shadow: 2px 2px 5px #00000073, -1px -1px 4px #6b6b6b3b;
        transform: scale(1);
    }
}

.Body{
    width: 325px;
    background-color: #3D3D3D;
    border-radius: 15px;
    box-shadow: 10px 10px 21px #05050538, -10px -10px 20px #3f3f3f85;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.ScreenSection{
    
}
.ButtonSection{
    width: 340px;
    background-color: transparent;
    margin: auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.OperationS{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    width: 300px;
    flex-wrap: wrap;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 90px;
    padding: 10px 0;
    margin: 10px 0;
    transition: 2s;
}
.block{
    font-family: monospace;
    color: #b3b3b3;
    background-color: #636363;
    padding: 3px;
    border-radius: 3px;
    margin: 2px;
    font-weight: 700;
    font-size: 1rem;
    box-shadow: 0px 0px 11px #161616a8;
    transition: 0.2s;
    animation: blockAppear 0.2s ease-out backwards;
}
.newClass{
}

@keyframes blockAppear{
    0%{
        opacity: 0;
        transform: translateX(50px);
    }
    100%{
        opacity: 1;
        transform: translateX(0);
    }
}

.OperationS::-webkit-scrollbar{
    width: 5px;
}
.OperationS::-webkit-scrollbar-track{

}
.OperationS::-webkit-scrollbar-thumb{
    border-radius: 5px;
    background-color: #636363;
}
.AnswerS{
    height: 50px;
    border-bottom: 1px solid #323232;
    font-size: 1.5rem;
    font-family: 'Montserrat';
    font-weight: 900;
    color: #34658b;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    overflow-x: scroll;
}
.AnswerS::-webkit-scrollbar{
    height: 5px;
}
.AnswerS::-webkit-scrollbar-track{

}
.AnswerS::-webkit-scrollbar-thumb{
    border-radius: 5px;
    background-color: #636363;
}