.App{
    background-color: aliceblue;
    min-height: 100vh;
}
.home{
    font-size: 1.5rem;
    font-family: Raleway;
    position: absolute;
    top: 20px;
    left: 20px;
    cursor: pointer;
    animation: rotation 3s linear infinite;
    z-index: 2;
}
.reactIcon{
    animation: reactIconAnimate 10s linear infinite;
}
@keyframes reactIconAnimate {
    0%{
        color: white;
    }
    20%{
        color: rgb(69, 142, 211);
    }
    
    40%{
        color: rgb(60, 190, 99);
    }
    60%{
        color: rgb(73, 73, 73);
    }
    80%{
        color: rgb(196, 87, 87);
    }
    100%{
        color: white;
    }
}
@keyframes rotation {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}