.Area{
    /* width: 40%; */
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Box{
    width: 280px;
    background-color: white;
    height: 435px;
    border-radius: 20px;
    box-shadow: 0 3px 6px #cacaca;
    padding: 20px 0;
    position: relative;
}
.words{
    display: flex;
    flex-flow: column;
    overflow: hidden;
}
.Typed{
    height: 42%;
    flex-flow: column;
    font-size: 1.7rem;
    justify-content: flex-end;
}
.Typing{
    overflow: initial;
    height: 13%;

}
.ToBeType{
    height: 45%;
    font-size: 1.5rem;
    position: relative;
}
.drop{
    background: linear-gradient(to bottom, #ffffff14, #ffffffd1);
    position: absolute;
    height: 100%;
    width: 100%;
}
.dropOver{
    background-color: #000000d6;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 10;
    margin-top: -20px;
    border-radius: 20px;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    color: #B1B1B1;
}
.dropOver .top{
    text-align: center;
    margin: 150px;
}
.dropOver .bottom{
    transition: 0.3s;
}
.dropOver .value{
    font-size: 4rem;
}
.dropOver .label{
    font-size: 1rem;
    color: #a7a7a799;
    font-weight: 100;
}
.dropOver2{
    background-color: #000000d6;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 10;
    margin-top: -20px;
    border-radius: 20px;
    padding: 40px 0;
    box-sizing: border-box;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    color: #B1B1B1;
}
.BeforeStart{
    font-size: 1rem;
    color: #ededed;
    text-transform: uppercase;
    user-select: none;
}