.TimeB{
    display: flex;
    justify-content: space-between;
    width: 400px;
    margin: 20px auto;
    margin: 10px auto 40px;
}
.Button{
    padding: 10px 30px;
    border-radius: 5px;
    background: white;
    box-shadow: 2px 2px 0 #ccc; 
    cursor: pointer;
    text-transform: uppercase;
    color: #777777;
    user-select: none;
}
.Button:hover{
    background-color: #A4DFEE;
    color: white;
}
.Button:active{
    transform: translate(1px, 1px);
    box-shadow: none;
}