.Button{
    height: 60px;
    width: 60px;
    color: #CCCCCC;
    font-family: "Montserrat", sans-serif;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 6px 6px 4px #17171736, -3px -3px 3px #7d7d7d18;
    margin: 10px;
    font-size: 1.2rem;
    font-weight: 600;
    user-select: none;
}
.Button:hover{
    /* background-color: #216E66; */
    cursor: pointer;
}
.Button:active{
    box-shadow: inset 6px 6px 4px #17171731,inset -3px -3px 3px #7d7d7d18;
}