.Event{
    /* display: flex; */
    width: 350px;
    position: relative;
}
.EventNull{
    width: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #989bac;
    font-family: Raleway;
    font-size: 1rem;
    position: relative;
    text-align: center;
}
.Header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
}
.Day{
    color: white;
    font-size: 1.2rem;
    font-weight: 800;
    font-family: Raleway;
}
.Date{
    color: #707384;
    font-family: Raleway;
    font-style: italic;
    font-weight: 800;
}
.Add{
    color: #707384;
    cursor: pointer;
    position: absolute;
    bottom: 15px;
    right: 15px;
    transition: 0.3s;
    font-size: 1.5rem;
}
.Add:hover{
    color: #4eb38d;
}

.Main{
    display: flex;
    flex-direction: column;
    font-family: Raleway;
    font-weight: bold;
}
.EventElement{
    padding: 10px 20px;
    background: linear-gradient(to right, #373C4F, #353B4D);   
}
.EventElement:nth-child(odd){
    background: linear-gradient(to right, #3E4558, #353B4D);   
}
.EventElement p{
    margin: 5px 0;
}
.eventName{
    color: white;
}
.eventName i{
    font-size: 0.6rem;
    margin-right: 10px;
}
.eventTime{
    color: rgb(167, 167, 167);
}
/* //create Event */
.CreateEvent{
    background-color: #3E4558;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    box-shadow: 0 0 6px #17191f91;
    color: white;
    font-family: Raleway;
    font-weight: 800;
    animation: showCreate 0.3s ease-out backwards;
}
@keyframes showCreate{
    0%{
        left: 50%;
        top: 50%;
        opacity: 0;
        transform: translate(-50%, -20%);
    }
    100%{
        left: 50%;
        top: 50%;
        opacity: 1;
        transform: translate(-50%, -50%);
    }
}
.CreateEvent form{
    padding: 20px;
}
.CreateEvent input{
    width: 100%;
    font-size: 1.1rem;
    padding: 5px;
    box-sizing: border-box;
    margin: 3px 0;
    background-color: #707384;
    border-radius: 2px;
    outline: none;
    color: white;
    border: none;
}
.CreateEvent input[name=event_name]{
    width: 250px;
}
.CreateEvent label{
    font-size: 0.9rem;
}
.CreateEvent .Field{
    margin: 5px 0;
    display: flex;
    flex-flow: column;
    align-items: flex-start;
    box-sizing: border-box;
    padding: 5px;
}
.CreateEvent .Group{
    display: flex;
    justify-content: space-between;
}
.previewDate{
    padding: 0 20px;
    margin: 0;
    font-size: 0.7rem;
    color: #8d9097;
}
.buttons{
    display: flex;
    margin: 10px 0 0 0;
}
.buttons button{
    width: 50%;
    padding: 20px 10px;
    background-color: #3E4558;
    color: white;
    border: none;
    cursor: pointer;
    font-family: Raleway;
    font-weight: 800;
    transition: 0.3s;
}
.buttons button:hover{
    color: white;
    border: none;
    background-color: #2d3344;
}
.buttons button:nth-child(1){
    border-right: 1px solid white;
}
.buttons button:nth-child(2){
    border-left: 1px solid white;
}
