.Status{
    width: 30%;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: flex-start;
    border-left: 2px solid #cccccc;
    padding: 0 0 0 20px;
    box-sizing: border-box;
}
.Info{
    display: flex;
    flex-flow: column;
    margin: 10px 0;
}
.label{
    color: #737373;
    font-size: 0.7rem;
    letter-spacing: 0.2rem;
}
.value{
    font-size: 2.5rem;
    color: #606060;
    letter-spacing: 0.5rem;

}