.Typing{
    font-family: sans-serif;
    height: 100vh;
    background-color: #EFEFEF;
}
.Head{
    box-sizing: border-box;
    width: 100%;
    padding: 20px;
    color: #7b7b7b;
    text-align: center;
    font-size: 1.2rem;
    letter-spacing: 0.8rem;
    text-transform: uppercase;
    text-shadow: 0 0 5px grey;
    text-shadow: 0 1px 2px #c3c3c3;
}
.Main{
    display: flex;
    width: 100%;
    justify-content: center;
}