.Calendar{
    background: linear-gradient(to bottom, rgb(245, 245, 245), #CCC7C1);
    min-height: 100vh;
    transition: 0.3s;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Area{
    transition: 1s;
    padding: 10px 0 10px 10px;
    background-color: #3E4558;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
}